.order_grid {
    display: grid;
    grid-template-columns: 2fr 3fr 2fr 2fr 2fr ;
    grid-template-rows: none;
}

.order_address {
    grid-column: 2;
}

.order_summary {
    grid-column: 4;
}


.payment_ccinput {
    grid-column: 2;
}

.payment_order {
    grid-column: 4;
}

.payment_holder {
    display: grid;
    grid-template-rows: none;
    grid-template-columns: 1fr 2fr 3fr 2fr 1fr;
}
