.confirm_fancy_font {
    font-family: Sacramento, cursive;
    font-style: normal;
    font-weight: 400;
    letter-spacing: 0;
    text-align: center;
    background: white;
    z-index: 2;
    position: relative;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 0 5% 8% 5%;
}

.confirm_fancy_confirm {
    font-size: 31px;
    line-height: 45px;
}

.confirm_fancy_title {
    font-size: 72px;
    line-height: 105px;
}

@-webkit-keyframes drive {
    from {
        left: 1vw;
        bottom: 53%;
    }
    to {
        left: 80vw;
        bottom: 53%;
    }
}

@keyframes drive {
    from {
        left: 1vw;
        bottom: 53%;
    }
    to {
        left: 80vw;
        bottom: 53%;
    }
}

.confirm_holder{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    grid-row: 2;
}

.confirm_holder > img {
    position: absolute;
    bottom: 53%;
    left: 1vw;

    z-index: 1;
    -webkit-animation: drive 2.5s;
            animation: drive 2.5s;
}

.confirm_holder_holder {
    display: grid;
    grid-template-rows: 2fr 7fr;
}
