.order_address_typeface {
    font-size: 18px;
    line-height: 22px;
    text-align: left;
}

.order_address_holder > p {
    margin: 0;
}

.order_saved_address {
    color: #FF5254;
    margin-bottom: 0.5%;
    font-weight: 700;
}

.order_input_holder {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: normal;

    margin-bottom: 2%;
    margin-top: 2%;
}

.order_input_item_holder {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    margin-bottom: 2%;
}

.order_input_item {
    display: flex;
    flex-direction: column;

    margin-right: 5%;
}

.order_input_item > * {
    width: 100%;
}

.order_address_title {
    font-size: 34px;
    font-weight: 600;
    line-height: 41px;
    margin-bottom: 2%;
}